import firebase from './../services/firebase'

const viewCounter = {
	data(){
		return {
			pageview: 0
		}
	},
	methods: {
		updatePageview(){
			const db = firebase.firestore()
			db.collection('view')
			.get()
			.then((q) => {
				let pv
				q.forEach((doc) => {
					pv = doc.data().pageview + 1
					this.pageview = pv
					this.setPageviewCount(pv)
					// console.log(pv)
				})
			})
			.catch((error) => {
				console.error("Error getting documents: ", error)
			});
		},
		setPageviewCount(pv){
			const db = firebase.firestore()
			db.collection('view')
			.doc('4m2nyBXcCnUvtgg2Oe3e')
			.update({
				pageview: pv
			})
			.then(() => {
				// console.log("Document successfully updated!")
			})
			.catch((error) => {
				console.error("Error updating document: ", error)
			})
		}
	}
}

export default viewCounter